.bookingpage{
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--textWhite);
    height: 100%;
    background-image:linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.9) ), url('../../assets/images/sparkles.jpg');
    background-position: top;
}
.booking-text{
    margin: 20px 0 0 0;
    width: 100%;
    padding: 40px 10%;
    text-align: center;
    color: var(--textWhite);
    h1{
        font-weight: 700;
        line-height: 28px;
        font-family: 'Poppins',Helvetica,Arial,Lucida,sans-serif;
        font-size: 18px;
    }

}

.bookingpage{
    .location{
        .location-card{
            .location-img-container{
                background-image: linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.8) ), url('../../assets/images/sambo_img_fuego.png');
                background-position: bottom;
            }
        }
    }
}

@media screen and (max-width:500px){

    .booking-text{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0;
        width: 100%;
        height: 90px;
        text-align: center;
        color: var(--textWhite);
        font-weight: 500;
        line-height: 28px;
    
    }


}